<script lang="ts" setup>
  import Nav from '../components/common/nav.vue'
  import Footer from '../components/common/footer.vue'
  import RotateButton from '../components/common/rotateButton.vue';
  import FlyPanel from '@/components/widgets/FlyPanel.vue';
  import { flexibleLayoutStore } from '@/store/flexible-layout';
  import { ref } from 'vue'
  import Aside from '@/components/a11y/Aside.vue';
  import Profile from '@/components/home/profile.vue';
  import ChatBot from '@/components/widgets/ChatBot.vue';
  import MultiTabs from '@/components/widgets/MultiSelectTab.vue';

  const layoutStore = flexibleLayoutStore();
  const featureTitle = ref('');
  const leftPanelCollapsed = ref(false);
  const rightPanelCollapsed = ref(false);
  const currentFeature = ref('');

  const layoutSwitch = () =>{
    leftPanelCollapsed.value= !leftPanelCollapsed.value;
    layoutStore.updateLayout('left-panel');
  }

  const switchFeature = (e: Event)=>{
    featureTitle.value = (e.target as HTMLElement).textContent!;
    layoutStore.updateLayout('right-panel');
    currentFeature.value = featureTitle.value;
  }
</script>

